import moment from 'moment';
import _ from 'lodash';

import { OpenBet } from '../models/BetOrder';
import { Result } from '../models/EventOdd';

const sync_Result_with_OpenBet_eventOdds = async () => {

	try{
		const openBets = await OpenBet.find({})
		if(!_.isEmpty(openBets)){
			await Promise.all(openBets.map(async openBet => {
				await Promise.all(openBet.eventOdds.map(async event => {
					const eventResult = await Result.findOne({ 'uniqueId': event.uniqueId })
					if(!_.isEmpty(eventResult)){
						await OpenBet.findOneAndUpdate({ orderNumber: openBet.orderNumber, eventOdds: { $elemMatch: { singlePickId: event.singlePickId }}}, 
							{ '$set': { 'eventOdds.$.score': eventResult.score, 'eventOdds.$.status': eventResult.status }})
						console.log('updated openBet with final score')
					}else{
						console.log(`result haven't save yet or start yet ${event.uniqueId} ${moment(event.matchTime).format('ddd, MMM DD, YYYY @ hh:mm A')}`)
					}
					return null
				}))
				return null
			}))
		}
			
	}catch(err){
		throw err
	}

}

export default sync_Result_with_OpenBet_eventOdds
